import sanitizeUrl from '../sanitizeUrl';

const whiteList = [
  'https://docs.dingtalk.com',
  'dingtalk://',
  'data://',
  './test/abc',
  'blob:',
  'alipays://12345'
];

describe('Utils.sanitizeUrl', () => {
  whiteList.forEach((p) => {
    it(`白名单内正常输出 ${p}'`, () => {
      const result = sanitizeUrl(p);
      expect(result).toEqual(p);
    });
  });

  it('不在白名单里输出字符', () => {
    const result = sanitizeUrl('test');
    expect(result).toEqual('test');
  });

  it('安全问题 url 不输出', () => {
    // eslint-disable-next-line no-script-url
    const result = sanitizeUrl('javascript:alert(cookie)');
    expect(result).toEqual('');
  });
});
